/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {FC} from 'react'
import {useIntl} from 'react-intl'
import {PageTitle} from '../../../_tagger-theme/layout/core'
import {
  MixedWidget2,
  MixedWidget10,
  MixedWidget11,
  ListsWidget2,
  ListsWidget3,
  ListsWidget4,
  ListsWidget5,
  ListsWidget6,
  TablesWidget5,
  TablesWidget10,
  MixedWidget8,
} from '../../../_tagger-theme/partials/widgets'

const FoodPage: FC = () => (
  <>

    <div className='row g-5 gx-xxl-8'>
      <div className='col-xxl-8'>
        <TablesWidget5 className='card-xxl-stretch mb-5 mb-xxl-8' />
      </div>
    </div>
  </>
)

const FoodWrapper: FC = () => {
  const intl = useIntl()
  return (
    <>
      <PageTitle description="Print food tags">{intl.formatMessage({id: 'MENU.FOOD'})}</PageTitle>
      <FoodPage />
    </>
  )
}

export {FoodWrapper}
