/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {FC} from 'react'
import {useIntl} from 'react-intl'
import {PageTitle} from '../../../_tagger-theme/layout/core'
import {
  MixedWidget2,
  MixedWidget10,
  MixedWidget11,
  ListsWidget2,
  ListsWidget3,
  ListsWidget4,
  ListsWidget5,
  ListsWidget6,
  TablesWidget5,
  TablesWidget10,
  MixedWidget8,
} from '../../../_tagger-theme/partials/widgets'

const CombinationPage: FC = () => (
  <>
    {/* begin::Row */}
    <div className='row gy-5 g-xl-8'>
      <div className='col-xxl-4'>
        <MixedWidget2
          className='card-xl-stretch mb-xl-8'
          chartColor='danger'
          chartHeight='200px'
          strokeColor='#cb1e46'
        />
      </div>
      <div className='col-xxl-4'>
        <ListsWidget5 className='card-xxl-stretch' />
      </div>
    </div>
    {/* end::Row */}


  </>
)

const CombinationWrapper: FC = () => {
  const intl = useIntl()
  return (
    <>
      <PageTitle description="Create, edit and delete communications">{intl.formatMessage({id: 'MENU.COMBINATION'})}</PageTitle>
      <CombinationPage />
    </>
  )
}

export {CombinationWrapper}
