/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {FC} from 'react'
import {useIntl} from 'react-intl'
import {PageTitle} from '../../../_tagger-theme/layout/core'
import {
  MixedWidget2,
  MixedWidget10,
  MixedWidget11,
  ListsWidget2,
  ListsWidget3,
  ListsWidget4,
  ListsWidget5,
  ListsWidget6,
  TablesWidget5,
  TablesWidget10,
  MixedWidget8,
} from '../../../_tagger-theme/partials/widgets'

const CommunicationPage: FC = () => (
  <>


    <div className='row g-5 gx-xxl-8'>
      <div className='col-xxl-4'>
        <MixedWidget8
          className='card-xxl-stretch mb-xl-3'
          chartColor='success'
          chartHeight='150px'
        />
      </div>
      <div className='col-xxl-8'>
        <TablesWidget5 className='card-xxl-stretch mb-5 mb-xxl-8' />
      </div>
    </div>
  </>
)

const CommunicationWrapper: FC = () => {
  const intl = useIntl()
  return (
    <>
      <PageTitle description="Print combinations tags">{intl.formatMessage({id: 'MENU.COMMUNICATION'})}</PageTitle>
      <CommunicationPage />
    </>
  )
}

export {CommunicationWrapper}
