/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {FC} from 'react'
import {useIntl} from 'react-intl'
import {PageTitle} from '../../../_tagger-theme/layout/core'
import {
  MixedWidget2,
  MixedWidget10,
  MixedWidget11,
  ListsWidget2,
  ListsWidget3,
  ListsWidget4,
  ListsWidget5,
  ListsWidget6,
  TablesWidget5,
  TablesWidget10,
  MixedWidget8,
} from '../../../_tagger-theme/partials/widgets'
import {QuickPrint} from "./quickprint/QuickPrint";

const PrintPage: FC = () => (
  <>

    <div className='row g-5 gx-xxl-12'>
      <div className='col-xxl-12'>
        <QuickPrint className='card-xxl-stretch mb-5 mb-xxl-8' />
      </div>
    </div>
  </>
)

const PrintWrapper: FC = () => {
  const intl = useIntl()
  return (
    <>
      <PageTitle description="Quick Print">{intl.formatMessage({id: 'MENU.PRINT'})}</PageTitle>
      <PrintPage />
    </>
  )
}

export {PrintWrapper}
